import { defineStore } from "pinia";


export const dataStore = defineStore({
    id: 'pokemons',
    state: () => ({
        pokemons: [],
        users:[]
    }),
    actions: {
        async morePokemons() {
            const offset = Math.random() * (700 - 1) + 1
            const responseArray = await fetch(`https://pokeapi.co/api/v2/pokemon?limit=3&offset=${offset}`)
            const responseJson = await responseArray.json()
            const pokemonsMap = responseJson.results.map(async (item) => {
                const responseItem = await fetch(item.url)
                const info = await responseItem.json()
                info.stats.forEach((stat) => {
                    stat['percent_base'] = (100 * stat.base_stat) / 200
                })
                return {
                    item,
                    info
                }
            })
            const pokemons = await Promise.all(pokemonsMap)

            this.$state.pokemons = [
                ...pokemons,
                ...this.$state.pokemons,
            ]
        },
        async moreUsers(){
            const getUsers = await fetch('https://randomuser.me/api/?results=3')
            const dataUsers = await getUsers.json()
            //console.log(dataUsers)
            this.$state.users = [
                ...dataUsers.results,
                ...this.$state.users
            ]
        }
    },
    getters: {
        getPokemons(state) {
            return state.pokemons
        },
        getUsers(state){
            console.log(state)
            return state.users
        }
    },
})